<template>
  <div class="create-post">
    <div class="title"></div>
    <div class="title-warp">
      <span></span>
      <input type="text" v-model="title" placeholder="..." class="input">
    </div>
    <div class="topic-warp">
      <span></span>
      <select class="input" v-model="topic">
        <option v-for="topic in topicList" :value="topic.id" :key="topic.id">{{topic.title}}</option>
      </select>
    </div>
    <div class="editor-warp">
      <div id="editor-tool"></div>
      <div id="editor"></div>
    </div>
    <div class="submit-btn" @click="submit"></div>
  </div>
</template>

<script>
import E from "wangeditor";
import api from "../common/api";
export default {
  data() {
    return {
      topicList: [],
      title: "",
      topic: "",
      content: ""
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (!vm.$store.state.userId) {
        // alert("");
        vm.$router.push({ path: "/" });
      }
    });
  },
  async mounted() {
    let editor = new E("#editor-tool", "#editor");
    //  2M
    editor.customConfig.uploadImgMaxSize = 2 * 1024 * 1024;
    // 
    editor.customConfig.onchange = content => {
      this.content = content;
    };
    // 
    editor.customConfig.customUploadImg = function(files, insert) {
      // files  input 
      // insert  url 
      let formData = new FormData();
      formData.append("file", files[0]);
      api.upload(formData).then(res => {
        // 
        insert(res.data[0]);
      });
    };
    // 
    editor.customConfig.customAlert = function(info) {
      // info 
      alert("" + info);
    };
    editor.create();
    // 
    editor.txt.html(this.content);

    api
      .getTopicList()
      .then(res => {
        this.topicList = res.data.rows;
      })
      .catch(err => {
        this.$message({
          message: "",
          type: "error",
          center: true
        });
      });
  },
  methods: {
    async submit() {
      api.createPost(this.topic, this.title, this.content).then(res => {
        this.$message({
          message: "",
          type: "success",
          center: true
        });
        this.$router.push({ path: "/" });
      });
    }
  }
};
</script>

<style lang='scss'>
.create-post {
  position: relative;
  padding: 30px;
  box-shadow: 0 0 15px 0 #ccc;
  border-radius: 8px;
  .title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    padding-left: 10px;
    border-left: 10px solid #003399;
  }
  .title-warp,
  .topic-warp {
    display: flex;
    padding: 10px;
    span {
      display: block;
      margin-right: 15px;
      padding: 5px 0;
      font-size: 16px;
    }
    .input {
      width: 500px;
      border: none;
      outline: none;
      border-bottom: 1px solid #003399;
    }
  }
  .topic-warp {
    margin-bottom: 30px;
  }
  #editor {
    // flex: 1;
    height: 480px;
  }
  .submit-btn {
    margin: 30px auto;
    cursor: pointer;
    text-align: center;
    padding: 5px 0;
    width: 80px;
    background: #0088ee;
    color: #fff;
    border-radius: 30px;
  }
}
</style>
