import axios from 'axios';
import { Message } from 'element-ui';
import router from '../router'

let token = window.localStorage.getItem("access_token");
axios.defaults.headers.common['access_token'] = token;

axios.interceptors.response.use((response) => {
  const data = response.data;
  if (!data || (data.code && data.code != 200)) {
    Message({
      message: `Error${data.code && `(${data.code})`} : ${data.message || ''}`,
      center: true,
      type: 'error'
    });
    let err = new Error();
    err.code = data.code || 500;
    err.errMsg = data.message || '';
    throw err;
  }
  return data;
}, (err) => { // 200
  let errMsg = '';
  let code;
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
        errMsg = ''
        break;
      case 401:
        errMsg = ''
        break
      case 403:
        errMsg = ''
        break
      case 404:
        errMsg = `: ${err.response.config.url}`
        break
      case 408:
        errMsg = ''
        break
      case 500:
        errMsg = ''
        break
      case 501:
        errMsg = ''
        break
      case 502:
        errMsg = ''
        break
      case 503:
        errMsg = ''
        break
      case 504:
        errMsg = ''
        break
      case 505:
        errMsg = 'HTTP'
        break
      default:
        errMsg = ''
    }
  }
  code = (err && err.response && err.response.data && err.response.data.code) || err.response.status || 500;
  errMsg = (err && err.response && err.response.data && err.response.data.message) || errMsg || '';
  if (code == 401) {
    Message({
      message: ``,
      center: true,
      type: 'error'
    });
    window.localStorage.removeItem('user_id');
    window.localStorage.removeItem('user_name');
    window.localStorage.removeItem('access_token');
    router.push({ path: '/' });
  } else {
    Message({
      message: `Error${code && `(${code})`} : ${errMsg}`,
      center: true,
      type: 'error'
    });
  }
  return Promise.reject({ code, errMsg });
})



export default axios;